import React, { useState, useEffect, useRef } from 'react';
import TurkishFlag from './assets/turkish-flag.webp';
import { AiOutlineClose } from 'react-icons/ai';
import './index.scss';

const AfetMessage: React.FunctionComponent<any> = () => {
    const mourning = useRef<HTMLDivElement>(null);
    const [shown, setShown] = useState(false);
    useEffect(() => {
        const isShown = localStorage.getItem('mourningMessageShown');
        if (isShown === "shown") {
            setShown(true);
        } else {
            setShown(false);
        }
    }, []);


    if (shown) {
        return <div className="noMourning" />
    }

    return (
        <div ref={mourning} className="animate__animated animate__slideInDown animate__faster">
            <div className="container closeMessageContainer">
                <div className="closeMessage">
                    <button onClick={() => {
                        localStorage.setItem('mourningMessageShown', 'shown');
                        setShown(true);
                    }}>
                        <AiOutlineClose color="white" size="40px" />
                    </button>
                </div>
            </div>
            <div className="mourning-message">
                <div className='container'>
                    <h1>Milletimizin Başı Sağolsun</h1>
                    <div className='seperator' />
                    <br />
                    <h3>
                        Kahramanmaraş merkezli 10 ilde 7,7 ve 7,6 büyüklüğünde çok büyük depremler meydana gelmiştir.
                    </h3>
                    <br />
                    <br />
                    <h3>
                        Depremlerin ardından en büyüğü 6,6 büyüklüğünde olmak üzere an itibarıyla 312 artçı deprem meydana gelmiştir.
                    </h3>
                    <br />
                    <br />
                    <h3>
                        Depremler; Kahramanmaraş, Hatay, Osmaniye, Adıyaman, Gaziantep, Şanlıurfa, Diyarbakır, Malatya ve Adana başta olmak üzere çevre illerde yoğun şekilde hissedilmiştir.
                    </h3>
                    <br />
                    <br />
                    <h3>
                        24.000'in üzerinde can kaybı ve 80.000'in üzerinde yaralanma olmuştur. Bu acı tabloda kahrolmamak gerçekten mümkün değil. Tüm Türkiye bu durum karşısında tek vucüd olmuştur.
                    </h3>
                    <br />
                    <br />
                    <h3>
                        Allah'tan hayatını kaybedenlere rahmet, yaralananlara acil şifalar diliyorum.
                    </h3>
                    <br />
                    <div className="turkishFlag">
                        <img
                            className="turkishFlagImage"
                            src={TurkishFlag}
                            alt="Turkish Flag"
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AfetMessage;